@use "../abstract/mixins" as m;
@use "../abstract/variables" as v;

.pieces {
    align-self: center;
    position: relative;
    min-width: 500px;
    min-height: 650px;
    transform: scale(0.8);

    @include m.tablet {
        transform: scale(0.9);
    }

    @include m.desktop {
        transform: scale(1);
    }

    .line {
        position: absolute;
        border: 1px dashed v.$gray-200;

        &.top,
        &.bottom {
            left: 50%;
            width: 1px;
            height: 32%;
            transform: translateX(-50%);
        }

        &.top {
            top: 0;
        }

        &.bottom {
            bottom: 0;
        }

        &.left,
        &.right {
            top: 50%;
            width: 36%;
            height: 1px;
            transform: translateY(-50%);
        }

        &.left {
            left: 0;
        }

        &.right {
            right: 0;
        }
    }

    .group {
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
        position: absolute;
        max-width: 110px;
        padding: 10px 20px;
        border: 2px solid v.$blue-700;
        background-color: v.$white;
        color: v.$blue-900;
        border-radius: 25px;
        opacity: 0.4;
        transition: opacity 0.3s ease;
        font-weight: 500;

        &:hover,
        &.active {
            opacity: 1;
            border-color: v.$blue-900;
            color: v.$blue-900;
        }

        &.top-left {
            top: -60px;
            left: 65px;

            @include m.tablet {
                top: 0;
                left: 0;
            }
        }

        &.top-right {
            top: -60px;
            right: 65px;

            @include m.tablet {
                top: 0;
                right: 0;
            }
        }

        &.bottom-left {
            bottom: -60px;
            left: 65px;

            @include m.tablet {
                bottom: 0;
                left: 0;
            }
        }

        &.bottom-right {
            bottom: -60px;
            right: 65px;

            @include m.tablet {
                bottom: 0;
                right: 0;
            }
        }

        &.top {
            top: 225px;
            left: 50%;
            transform: translateX(-50%);
        }

        &.center {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &.bottom {
            top: 380px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    .piece {
        z-index: 2;
        position: absolute;

        &:hover,
        &.active {
            background-color: rgba(0, 102, 255, 0.05);

            svg {
                opacity: 1;
            }

            span {
                color: v.$blue-900;
                opacity: 1;
            }
        }

        svg {
            position: absolute;
            color: v.$blue-900;
            opacity: 0.4;
        }

        span {
            display: block;
            position: absolute;
            color: v.$blue-700;
            opacity: 0.4;
            font-size: 1.4rem;
        }
    }

